@font-face {
    font-family: 'minecraftiaregular';
    src: url('/fonts/font-file/minecraftia-regular-webfont.eot');
    src: url('/fonts/font-file/minecraftia-regular-webfont.eot?#iefix')
        format('embedded-opentype'),
      url('/fonts/font-file/minecraftia-regular-webfont.woff2') format('woff2'),
      url('/fonts/font-file/minecraftia-regular-webfont.woff') format('woff'),
      url('/fonts/font-file/minecraftia-regular-webfont.ttf') format('truetype'),
      url('/fonts/font-file/minecraftia-regular-webfont.svg#minecraftiaregular')
        format('svg');
    font-weight: normal;
    font-style: normal;
  }